.timeline-card {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
    position: relative;
    margin: 10px -27px;
    width: 50%;
}

.timeline-card:nth-child(odd) {
    align-self: flex-end;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 0;
}

.timeline-card-content {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    background-color: rgb(241, 241, 241);
    color: #222831;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 15px;
    position: relative;
    width: 400px;
    max-width: 70%;
    text-align: right;
}

.timeline-card-content::after {
    content: ' ';
    background-color:rgb(241, 241, 241);
    box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
    position: absolute;
    right: -7.5px;
    top: calc(50% - 5px);
    transform: rotate(45deg);
    width: 15px;
    height: 15px;
}

.timeline-card:nth-child(odd) .timeline-card-content {
    text-align: left;
    align-items: flex-start;
}

.timeline-card:nth-child(odd) .timeline-card-content::after {
    right: auto;
    left: -7.5px;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timeline-card-content time {
    color: #777;
    font-size: 12px;
    font-weight: bold;
}

.timeline-card-content p {
    font-size: 16px;
    line-height: 24px;
    margin: 15px 0;
    max-width: 250px;
}

.timeline-card-content h4 {
    color: #222831;
    margin: -6% 0 6% 0;
}

.timeline-card-content .timeline-circle {
    background-color: #fff;
    border: 3px solid #ffd369;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 10px);
    right: -40px;
    width: 20px;
    height: 20px;
    z-index: 9;
}

.timeline-card:nth-child(odd) .timeline-card-content .timeline-circle {
    right: auto;
    left: -40px;
}